.cred-story{
   min-height: 100vh;
   background-color: var(--bg-green);
   background-size: cover;
   background-position:right bottom;
   position: relative;
   background-image: url(https://web-images.credcdn.in/_next/assets/images/home-page/trust-bg.jpg);
}
.story-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top:20px;
}

.cred-story-heading-wrapper{
    width: 50%;
    flex-direction: column;
    justify-content: flex-end;
}

.cred-story-heading{
    margin-top:30%;
    font-size: 60px;
    line-height: 60px;
    font-weight: 700px;
    max-width: 80%;
}

.cred-story-detail{
    width:100%;
    font-weight: 500;
    margin-top:45px;
    font-size: 20px;
}




@media all and (max-width:1279px)
{
    .cred-story-heading{
        max-width: 100%;
    }
}


@media all and (max-width:767px)
{
    .cred-story{
        background-image: url(https://web-images.credcdn.in/_next/assets/images/home-page/trust-bg-mobile.jpg);
    }
    .storywrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-top:-55px;
    }
    .cred-story-heading{
        font-size: 20px;
        font-weight: 500px;
        width: 100%;
        padding: 0px;
        margin-top:40px;
    }
    .cred-story-detail{
        width:100%;
        font-weight: 500;
        margin-right:80px;
        margin-left: 20px;
    }
    .cred-story-para{
        font-size: 15px;
    }
    
}